.documents-listing .view-content {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
.documents-listing .view-content .views-row {
  display: flex;
}
.documents-listing .view-content .views-row a {
  color: var(--color-black);
  border: 1px solid var(--color-gray);
  border-radius: 10px;
  padding: 20px 15px;
  font-weight: bold;
  display: block;
}
.documents-listing .view-content .views-row a:before {
  content: "";
  background: url("../../../../icons/file.svg") no-repeat center;
  width: 12px;
  height: 15px;
  display: inline-block;
  margin-right: 15px;
}

.dark-mode .documents-listing .view-content .views-row a:before {
  background: url("../../../../icons/file-white.svg") no-repeat center;
}